import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import { BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Bar, ResponsiveContainer, Legend, ReferenceLine, LabelList } from 'recharts';

const PlayerSearch = () => {
    const [players, setPlayers] = useState([]);
    const [search, setSearch] = useState("");
    const [timer, setTimer] = useState(null);
    const [selectedPlayerStats, setSelectedPlayerStats] = useState([]);
    const [selectedPlayer, setSelectedPlayer] = useState(null);
    const [startDate, setStartDate] = useState("");
    const [endDate, setEndDate] = useState("");
    const [selectedStat, setSelectedStat] = useState("pts");
    const [referenceLine, setReferenceLine] = useState(null);

    useEffect(() => {
        const fetchPlayers = async () => {
            const response = await axios.get('https://www.balldontlie.io/api/v1/players');
            setPlayers(response.data.data.map(player => ({ id: player.id, name: player.first_name + ' ' + player.last_name })));
        };
        fetchPlayers();
    }, []);

    useEffect(() => {
        if (timer) {
            clearTimeout(timer);
        }

        if (search) {
            setTimer(setTimeout(async () => {
                const response = await axios.get(`https://www.balldontlie.io/api/v1/players?search=${search}`);
                setPlayers(response.data.data.map(player => ({ id: player.id, name: player.first_name + ' ' + player.last_name })));
            }, 500));
        }
    }, [search]);

    useEffect(() => {
        const fetchPlayerStats = async () => {
            if (selectedPlayer) {
                let url = `https://www.balldontlie.io/api/v1/stats?player_ids[]=${selectedPlayer.id}`;
                if (startDate) url += `&start_date=${startDate}`;
                if (endDate) url += `&end_date=${endDate}`;
                const response = await axios.get(url);
                const stats = response.data.data;
                stats.sort((a, b) => new Date(a.game.date) - new Date(b.game.date));
                setSelectedPlayerStats(stats);
            } else {
                setSelectedPlayerStats([]);
            }
        };
        fetchPlayerStats();
    }, [selectedPlayer, startDate, endDate]);

    const handlePlayerSelect = (event, value) => {
        setSelectedPlayer(value);
    };

    return (
        <div style={{ margin: '7px 5px' }}>
            <Autocomplete
                freeSolo
                options={players}
                getOptionLabel={(option) => option.name}
                onInputChange={(event, value) => setSearch(value)}
                onChange={handlePlayerSelect}
                renderInput={(params) => <TextField {...params} label="Wyszukaj zawodnika..." variant="outlined" style={{ marginBottom: '20px' }} />}
            />
            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '20px' }}>
                <TextField
                    label="Data początkowa"
                    type="date"
                    value={startDate}
                    onChange={(event) => setStartDate(event.target.value)}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
                <TextField
                    label="Data końcowa"
                    type="date"
                    value={endDate}
                    onChange={(event) => setEndDate(event.target.value)}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
            </div>
            <RadioGroup row value={selectedStat} onChange={(event) => setSelectedStat(event.target.value)}>
                <FormControlLabel value="pts" control={<Radio />} label="Punkty" />
                <FormControlLabel value="ast" control={<Radio />} label="Asysty" />
                <FormControlLabel value="reb" control={<Radio />} label="Zbiórki" />
                <FormControlLabel value="fg3a" control={<Radio />} label="Trójki" />
                <FormControlLabel value="pra" control={<Radio />} label="PRA" />
            </RadioGroup>
            <TextField
                label="Linia"
                type="number"
                value={referenceLine}
                onChange={(event) => setReferenceLine(event.target.value)}
                style={{ marginBottom: '20px' }}
            />
            <ResponsiveContainer width="100%" height={400}>
                <BarChart data={selectedPlayerStats.map(stat => ({ date: new Date(stat.game.date).toLocaleDateString(), stat: selectedStat === 'pra' ? stat.pts + stat.ast + stat.reb : stat[selectedStat] }))}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="date" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Bar dataKey="stat" fill="#8884d8">
                        <LabelList dataKey="stat" position="top" />
                    </Bar>
                    {referenceLine && <ReferenceLine y={referenceLine} stroke="red" />}
                </BarChart>
            </ResponsiveContainer>
        </div>
    );
};

export default PlayerSearch;